import Xpath from './xpath';
import { FieldType } from './trainerModels';

export class Field
{
  constructor(parent, htmlElement, type)
  {
    this.parent = parent;
    this.htmlElement = htmlElement;
    this.type = type;
    this.manualLoginScore = 0;
    this.trainingHtmlElement = undefined;
    if (htmlElement !== undefined)
    {
      this.id = htmlElement.id;
      this.name = htmlElement.name;
      this.scores = [];
      this.xpath = Xpath.getElementXPath(htmlElement);

      // Create a FDProcessedID field, to let us know which elements have been processed by the library.
      if (htmlElement.hasAttribute('FDProcessedID'))
      {
        this.FDProcessedID = htmlElement.getAttribute('FDProcessedID');
      }
      else
      {
        const fdpid = Math.random().toString(36).substring(7);
        htmlElement.setAttribute('FDProcessedID', fdpid);
        this.FDProcessedID = fdpid;
      }
    }
  }

  getId()
  {
    return this.id;
  }

  getFDProcessedID()
  {
    return this.FDProcessedID;
  }

  getName()
  {
    return this.name;
  }

  getHtmlElement()
  {
    return this.htmlElement;
  }

  getEditableHtmlElement()
  {
    if (this.htmlElement instanceof HTMLInputElement || this.htmlElement instanceof HTMLSelectElement)
    {
      return this.htmlElement;
    }

    return {};
  }

  getParent()
  {
    return this.parent;
  }

  getType()
  {
    return this.type;
  }

  matchesType(fieldType)
  {
    if (fieldType === this.type)
    {
      return true;
    }
    if (fieldType === FieldType.Select || fieldType === FieldType.Text || fieldType === FieldType.TextOrSelect)
    {
      return this.type === FieldType.Select || this.type === FieldType.Text || this.type === FieldType.TextOrSelect;
    }
    if (fieldType === FieldType.TextOrPassword)
    {
      return this.type === FieldType.Text || this.type === FieldType.Password || this.type === FieldType.TextOrPassword;
    }
    return false;
  }
}

export class ClickableField extends Field
{
  constructor(parent, htmlElement)
  {
    super(parent, htmlElement, FieldType.Button);
    this.parent = parent;
    this.htmlElement = htmlElement;
    this.clickedTime = undefined;
  }
}

export class PageInfo
{
  constructor()
  {
    this.fields = [];
    this.clickableFields = [];
    this.forms = [];
    this.forced = false;
    this.url = window.location.href;
  }
}

export class FieldInfo
{
  constructor(fieldDescription, field)
  {
    this.fieldDescription = fieldDescription;
    this.field = field;
  }
}

export class ExportableField
{
  constructor(field)
  {
    this.id = field.getId();
    this.name = field.getName();
    this.type = field.getType();
    this.scores = field.scores;
  }
}

export class ExportableFieldInfo
{
  constructor(fieldDescription, field)
  {
    this.fieldDescription = fieldDescription;
    this.field = field;
  }
}

export class FormInfo
{
  constructor()
  {
    this.fieldInfos = [];
    this.exception = false;
    this.averageScore = 0;
  }

  grabParentForms()
  {
    const forms = this.fieldInfos.filter((_) => _.field.getParent() !== undefined).map((_) => _.field.getParent());
    const uniqueForms = [];
    forms.forEach((f) =>
    {
      if (f instanceof HTMLFormElement)
      {
        if (!uniqueForms.some((_) => _ === f))
        {
          uniqueForms.push(f);
        }
      }
    });
    return uniqueForms;
  }

  getExportableObj()
  {
    return new ExportableFormInfo(this);
  }
}

export class ExportableFormInfo
{
  constructor(formInfo)
  {
    this.formDescription = formInfo.formDescription;
    this.fieldInfos = formInfo.fieldInfos.map((f) => new ExportableFieldInfo(f.fieldDescription, new ExportableField(f.field)));
  }
}

