export class LoginItemMetadata extends VaultItemMetadata
{
  constructor(website, name, loginUrl = '', favicon = '', frameUrl = '')
  {
    super();
    this.website = website;
    this.loginUrl = loginUrl;
    this.favicon = favicon;
    this.frameUrl = frameUrl;
    this.username = '';
    this.originatingTabUrl = '';
    this.type = 'LoginItem';
    this.name = name;
  }
}

export class CreditCardContent
{
}

export class CreditCardItem extends VaultItem
{
  static isSame(item1, item2)
  {
    const valuesToCompare = [
      [item1.content.cardholder, item2.content.cardholder],
      [item1.content.number, item2.content.number],
      [item1.content.verification, item2.content.verification],
      [item1.content.pin, item2.content.pin],
      [item1.content.expiryMonth, item2.content.expiryMonth],
      [item1.content.expiryYear, item2.content.expiryYear]
    ];
    for (let index = 0; index < valuesToCompare.length; index++)
    {
      const item = valuesToCompare[index];
      if (item[0] !== item[1])
      {
        return false;
      }
    }
    return true;
  }
}

export class LoginItem extends VaultItem
{
  constructor()
  {
    super();
    this.content = {};
  }

  static isSame(item1, item2)
  {
    const valuesToCompare = [
      [item1.metadata.username, item2.metadata.username],
      [item1.metadata.favicon, item2.metadata.favicon],
      [item1.metadata.loginUrl, item2.metadata.loginUrl],
      [item1.content.password, item2.content.password]
    ];
    for (let index = 0; index < valuesToCompare.length; index++)
    {
      const item = valuesToCompare[index];
      if (item[0] !== item[1])
      {
        return false;
      }
    }
    return true;
  }
}

export class PendingVaultItem
{
  constructor(vaultItem, url)
  {
    this.vaultItem = vaultItem;
    this.url = url;
  }
}
