export default class Xpath
{
  static getElementXPath(element)
  {
    if (element && element.id) return `//*[@id="${element.id}"]`;
    return Xpath.getElementTreeXPath(element);
  }

  static getElementTreeXPath(element)
  {
    const paths = [];
    for (; element && element.nodeType === Node.ELEMENT_NODE; element = element.parentNode)
    {
      let index = 0;
      let hasFollowingSiblings = false;
      for (let sibling = element.previousSibling; sibling; sibling = sibling.previousSibling)
      {
        if (sibling.nodeType === Node.DOCUMENT_TYPE_NODE) continue;
        if (sibling.nodeName === element.nodeName) ++index;
      }
      for (let sibling = element.nextSibling; sibling && !hasFollowingSiblings; sibling = sibling.nextSibling)
      {
        if (sibling.nodeName === element.nodeName) hasFollowingSiblings = true;
      }
      const tagName = (element.prefix ? `${element.prefix}:` : '') + element.localName;
      const pathIndex = (index || hasFollowingSiblings ? `[${index + 1}]` : '');
      paths.splice(0, 0, tagName + pathIndex);
    }
    return paths.length ? `/${paths.join('/')}` : null;
  }

  static cssToXPath(rule)
  {
    const regElement = /^([#.]?)([a-z0-9\\*_-]*)((\|)([a-z0-9\\*_-]*))?/i;
    const regAttr1 = /^\[([^\]]*)\]/i;
    const regAttr2 = /^\[\s*([^~=\s]+)\s*(~?=)\s*"([^"]+)"\s*\]/i;
    const regPseudo = /^:([a-z_-])+/i;
    const regCombinator = /^(\s*[>+\s])?/i;
    const regComma = /^\s*,/i;
    let index = 1;
    const parts = ['//', '*'];
    let lastRule = null;
    while (rule.length && rule !== lastRule)
    {
      lastRule = rule;
      rule = String.trim(rule);
      if (!rule.length) break;
      let m = regElement.exec(rule);
      if (m)
      {
        if (!m[1])
        {
          if (m[5]) parts[index] = m[5]; // Eslint was giving an error for array deconstructing, since I didn't want to unpack all 5 potential values, I added to eslint rules "prefer-destructuring"
          else parts[index] = m[2];
        }
        else if (m[1] === '#') parts.push(`[@id='${m[2]}']`);
        else if (m[1] === '.') parts.push(`[contains(concat(' ',normalize-space(@class),' '), ' ${m[2]} ')]`);
        rule = rule.substr(m[0].length);
      }
      m = regAttr2.exec(rule);
      if (m)
      {
        if (m[2] === '~=') parts.push(`[contains(@${m[1]}, '${m[3]}')]`);
        else parts.push(`[@${m[1]}='${m[3]}']`);
        rule = rule.substr(m[0].length);
      }
      else
      {
        m = regAttr1.exec(rule);
        if (m)
        {
          parts.push(`[@${m[1]}]`);
          rule = rule.substr(m[0].length);
        }
      }
      m = regPseudo.exec(rule);
      while (m)
      {
        rule = rule.substr(m[0].length);
        m = regPseudo.exec(rule);
      }
      m = regCombinator.exec(rule);
      if (m && m[0].length)
      {
        if (m[0].indexOf('>') !== -1) parts.push('/');
        else if (m[0].indexOf('+') !== -1) parts.push('/following-sibling::');
        else parts.push('//');
        index = parts.length;
        parts.push('*');
        rule = rule.substr(m[0].length);
      }
      m = regComma.exec(rule);
      if (m)
      {
        parts.push(' | ', '//', '*');
        index = parts.length - 1;
        rule = rule.substr(m[0].length);
      }
    }
    const xpath = parts.join('');
    return xpath;
  }

  static getElementsBySelector(doc, css)
  {
    const xpath = Xpath.cssToXPath(css);
    return Xpath.getElementsByXPath(doc, xpath);
  }

  static getElementsByXPath(doc, xpath)
  {
    try
    {
      return Xpath.evaluateXPath(doc, xpath, undefined, undefined);
    }
    catch (ex)
    {
      return [];
    }
  }

  static evaluateXPath(doc, xpath, contextNode, resultType)
  {
    if (contextNode === undefined)
    {
      contextNode = doc;
    }

    if (resultType === undefined)
    {
      resultType = XPathResult.ANY_TYPE;
    }

    const result = doc.evaluate(xpath, contextNode, null, resultType, null);
    const nodes = [];
    switch (result.resultType)
    {
      case XPathResult.NUMBER_TYPE:
        return result.numberValue;
      case XPathResult.STRING_TYPE:
        return result.stringValue;
      case XPathResult.BOOLEAN_TYPE:
        return result.booleanValue;
      case XPathResult.UNORDERED_NODE_ITERATOR_TYPE:
      case XPathResult.ORDERED_NODE_ITERATOR_TYPE:
        for (let item = result.iterateNext(); item; item = result.iterateNext()) nodes.push(item);
        return nodes;
      case XPathResult.UNORDERED_NODE_SNAPSHOT_TYPE:
      case XPathResult.ORDERED_NODE_SNAPSHOT_TYPE:
        for (let i = 0; i < result.snapshotLength; ++i) nodes.push(result.snapshotItem(i));
        return nodes;
      case XPathResult.ANY_UNORDERED_NODE_TYPE:
      case XPathResult.FIRST_ORDERED_NODE_TYPE:
        return result.singleNodeValue;
    }

    return nodes;
  }

  static getRuleMatchingElements(rule, doc)
  {
    const css = rule.selectorText;
    const xpath = Xpath.cssToXPath(css);
    return Xpath.getElementsByXPath(doc, xpath);
  }
}
