import * as Matcher from './matcher';
import { FieldType, VaultItemType, FieldDescription } from './trainerModels';
import { ClickableField, Field, FormInfo, PageInfo, FieldInfo } from './models';
import ScrapingServiceImpl from './scraper';
import Xpath from './xpath';
import { FDBoolean, FDHtmlElement, FDWindow, CommonUtils } from './utils';
import { Globals } from './globals';

/**
 * Scoring Service. Training model's weights and exceptions are store in this class.
 * Using Singleton pattern to persist weights and exceptions for all services.
 */
export default class ScoringServiceImpl
{
  constructor()
  {
    if (ScoringServiceImpl.exists)
    {
      return ScoringServiceImpl.instance;
    }

    this.scraper = new ScrapingServiceImpl();

    this.exceptions = CommonUtils.combineExceptions(Globals.Exceptions.default);
    this.fieldsTraining = [];
    this.inputFields = [];
    this.selectFields = [];
    this.passwordFields = [];
    this.clickableFields = [];
    this.crfParams = Globals.Weights.crf;

    ScoringServiceImpl.exists = true;
    ScoringServiceImpl.instance = this;
  }

  fetchFormInfoTypes()
  {
    return [
      {
        name: 'login',
        priority: 3,
        isTrainable: true,
        isScorable: true,
        vaultItemType: VaultItemType.LoginItem,
        fields: [
          new FieldDescription('username', FieldType.Text, 'Login - username'),
          new FieldDescription('password', FieldType.Password, 'Login - password'),
          new FieldDescription('submit', FieldType.Button, 'Login - submit')
        ]
      },
      {
        name: 'signup',
        priority: 2,
        isTrainable: true,
        isScorable: true,
        vaultItemType: VaultItemType.LoginItem,
        fields: [
          new FieldDescription('username', FieldType.Text, 'Signup - username'),
          new FieldDescription('password', FieldType.Password, 'Signup - password'),
          new FieldDescription('submit', FieldType.Button, 'Signup - submit')
        ]
      },
      {
        name: 'newsletter',
        priority: 0,
        isTrainable: true,
        isScorable: true,
        vaultItemType: VaultItemType.Other,
        fields: [
          new FieldDescription('email', FieldType.Text, 'Newsletter - email'),
          new FieldDescription('submit', FieldType.Button, 'Newsletter - submit')
        ]
      },
      {
        name: 'resetpassword',
        priority: 0,
        isTrainable: true,
        isScorable: true,
        vaultItemType: VaultItemType.LoginItem,
        fields: [
          new FieldDescription('username', FieldType.Text, 'Reset Password - username'),
          new FieldDescription('submit', FieldType.Button, 'Reset Password - submit')
        ]
      },
      {
        name: 'search',
        priority: 0,
        isTrainable: true,
        isScorable: false,
        vaultItemType: VaultItemType.Other,
        fields: [
          new FieldDescription('query', FieldType.Text, 'Search - input query'),
          new FieldDescription('submit', FieldType.Button, 'Search - submit')
        ]
      },
      {
        name: 'creditcard',
        priority: 1,
        vaultItemType: VaultItemType.CreditCardItem,
        isTrainable: true,
        isScorable: true,
        fields: [
          new FieldDescription('cc_number', FieldType.Text, 'CC - number'),
          new FieldDescription('cc_name', FieldType.Text, 'CC - name'),
          new FieldDescription('cc_firstname', FieldType.Text, 'CC - first name'),
          new FieldDescription('cc_lastname', FieldType.Text, 'CC - last name'),
          new FieldDescription('cc_mm', FieldType.TextOrSelect, 'CC - mm'),
          new FieldDescription('cc_yy', FieldType.TextOrSelect, 'CC - yy'),
          new FieldDescription('cc_date', FieldType.TextOrSelect, 'CC - date'),
          new FieldDescription('cc_cvc', FieldType.TextOrPassword, 'CC - cvc')
        ]
      },
      {
        name: 'changepassword',
        priority: 2,
        vaultItemType: VaultItemType.LoginItem,
        isTrainable: true,
        isScorable: true,
        fields: [
          new FieldDescription('username', FieldType.Text, 'Change Password - username'),
          new FieldDescription('oldpassword', FieldType.Password, 'Change Password - old password'),
          new FieldDescription('newpassword', FieldType.Password, 'Change Password - new password'),
          new FieldDescription('confirmpassword', FieldType.Password, 'Change Password - confirm password'),
          new FieldDescription('submit', FieldType.Button, 'Change Password - submit')
        ]
      }
    ];
  }

  /**
   * Assigns the scraped fields feature descriptions
   * @param {Object[]} fields - Scraped fields from the webpage
   * @param {Object[]} featuresDescription - All the feature descriptions avaiable for various fields
   */
  setFeaturesForFields(fields, featuresDescription)
  {
    for (const field of fields)
    {
      field.featuresDescription = [];
      field.features = this.getFeaturesForField(field);
      field.features.forEach((feature, index) =>
      {
        field.featuresDescription.push([featuresDescription[index], feature]);
      });
    }

    return fields;
  }

  getCRFFormInfos(fields)
  {
    const result = [];
    const formTypes = this.fetchFormInfoTypes();
    const observations = fields.map((_) => _.features);
    const crfParams = this.getCRFParams();
    const { transition_probs: crfTransitionProbs, classes } = crfParams;

    const viterbiResults = this.viterbi(crfTransitionProbs, observations, crfParams.weights, classes);

    viterbiResults.forEach((guess, index) =>
    {
      if (guess !== 'other:other')
      {
        const [formType, fieldName] = guess.split(':');
        const formDescription = formTypes.find((_) => _.name === formType);
        if (formDescription)
        {
          const fieldDescription = formDescription.fields.find((_) => _.name === fieldName);
          let formInfo = result.find((_) => _.formDescription.name === formType);
          if (!formInfo)
          {
            formInfo = new FormInfo();
            formInfo.formDescription = formDescription;
            if (formInfo.formDescription)
            {
              result.push(formInfo);
            }
          }

          if (fieldDescription && fields[index].matchesType(fieldDescription.type))
          {
            formInfo.fieldInfos.push(new FieldInfo(fieldDescription, fields[index]));
          }
        }
      }
    });

    return result;
  }

  getFeaturesDescription()
  {
    if (!this.featuresDescription)
    {
      this.featuresDescription = this.getAllFeaturesDescription();
    }

    return this.featuresDescription;
  }

  getNumFeatures()
  {
    return this.getFeaturesDescription().length;
  }

  getPageInfo(forceLogin = false)
  {
    // reset the scraper's stored info as it doesn't contain the sign up popup's new fields
    this.scraper.clearFields();
    // for debugging
    this.inputFields = this.scraper.getAllTextFields();
    this.selectFields = this.scraper.getAllSelectFields();
    this.passwordFields = this.scraper.getAllPasswordFields();
    this.clickableFields = this.scraper.getAllClickeableFields();

    const formTypes = this.fetchFormInfoTypes();
    const fieldDescriptions = this.getFeaturesDescription();

    let fields = this.scraper.getAllFields();
    if (fields.length > 100)
    {
      fields = fields.slice(0, 100);
    }

    this.setFeaturesForFields(fields, fieldDescriptions);

    const pageInfo = new PageInfo();
    pageInfo.fields = fields;
    pageInfo.clickableFields = this.clickableFields;

    for (const formDescription of formTypes)
    {
      const formInfo = new FormInfo();
      formInfo.formDescription = formDescription;

      if (!FDWindow.isBrowser()) continue;

      const exceptions = this.getExceptions(formDescription.name);
      for (const e of exceptions)
      {
        if (e.ignore && e.ignore.length > 0)
        {
          let fieldsToIgnore = [];
          e.ignore.forEach((xpath) =>
          {
            const elements = Xpath.getElementsByXPath(document, xpath);
            fieldsToIgnore = fieldsToIgnore.concat(elements);
          });

          fieldsToIgnore.forEach((f) =>
          {
            if (pageInfo.fields.deleteFirst((_) => _.getHtmlElement() === f))
            {
              pageInfo.clickableFields.deleteFirst((_) => _.getHtmlElement() === f);
            }
          });
        }

        const formInfoFromException = this.getFormInfoFromException(e, formDescription);
        if (formInfoFromException)
        {
          pageInfo.forms.push(formInfoFromException);
        }
      }
    }

    this.getCRFFormInfos(pageInfo.fields).forEach((form) =>
    {
      pageInfo.forms.push(form);
    });

    if (pageInfo.forms.some((_) => _.exception === true) === true)
    {
      pageInfo.forms = pageInfo.forms.filter((_) => _.exception === true);
    }

    // Only grab text and password input fields
    pageInfo.forms = pageInfo.forms.filter((form) =>
    {
      const isValidType = form.fieldInfos.some((f) => f.fieldDescription.type === FieldType.Text || f.fieldDescription.type === FieldType.Password);
      if (!isValidType) return false;

      if (form.formDescription.name === 'creditcard')
      {
        return Boolean(form.fieldInfos.length > 1);
      }

      return true;
    });

    if (!pageInfo.forms.length && forceLogin)
    {
      const forcedLogin = this.getForcedLoginForm();
      if (forcedLogin)
      {
        pageInfo.forms = [forcedLogin];
        pageInfo.forced = true;
      }
    }

    return pageInfo;
  }

  getFakePageInfoWithCC(pageInfo)
  {
    const formInfo = new FormInfo();
    const formDescription = this.fetchFormInfoTypes().find((f) => f.name === 'creditcard');
    formInfo.formDescription = formDescription;
    const ccNumberDescription = formInfo.formDescription.fields.find((_) => _.name === 'cc_number');
    const ccNameDescription = formInfo.formDescription.fields.find((_) => _.name === 'cc_name');
    const ccMMDescription = formInfo.formDescription.fields.find((_) => _.name === 'cc_mm');
    const ccYYDescription = formInfo.formDescription.fields.find((_) => _.name === 'cc_yy');
    const ccCVCDescription = formInfo.formDescription.fields.find((_) => _.name === 'cc_cvc');
    const ccNumber = pageInfo.fields.find((_) => _.name === 'cardNumber');
    const ccHolder = pageInfo.fields.find((_) => _.name === 'cardholder');
    const ccMM = pageInfo.fields.find((_) => _.name === 'cardexpirymonth');
    const ccYY = pageInfo.fields.find((_) => _.name === 'cardexpiryyear');
    const ccCVC = pageInfo.fields.find((_) => _.name === 'cvc');
    formInfo.fieldInfos.push(new FieldInfo(ccNumberDescription, ccNumber));
    formInfo.fieldInfos.push(new FieldInfo(ccNameDescription, ccHolder));
    formInfo.fieldInfos.push(new FieldInfo(ccMMDescription, ccMM));
    formInfo.fieldInfos.push(new FieldInfo(ccYYDescription, ccYY));
    formInfo.fieldInfos.push(new FieldInfo(ccCVCDescription, ccCVC));
    pageInfo.forms.push(formInfo);

    Globals.Logger.log(pageInfo);
    return pageInfo;
  }

  getForcedLoginForm()
  {
    const inputFields = this.scraper.getAllTextFields();
    const passwordFields = this.scraper.getAllPasswordFields();
    let onlyInputAndPasswordFields = inputFields.concat(passwordFields);
    // const formTypes = this.fetchFormInfoTypes();
    const fieldDescription = this.getFeaturesDescription();
    onlyInputAndPasswordFields = this.setFeaturesForFields(onlyInputAndPasswordFields, fieldDescription);
    const formInfos = this.getCRFFormInfos(onlyInputAndPasswordFields);

    let result = formInfos.find((item) => item.formDescription.name === 'login');

    if (!result)
    {
      if (passwordFields.length > 0)
      {
        const tempFormInfos = [];
        const loginDescription = this.fetchFormInfoTypes().find((formDesc) => formDesc.name === 'login');
        const passwordDescription = loginDescription.fields.find((fieldDesc) => fieldDesc.name === 'password');
        const usernameDescription = loginDescription.fields.find((fieldDesc) => fieldDesc.name === 'username');
        passwordFields.forEach((passwordField) =>
        {
          const formInfo = new FormInfo();
          formInfo.formDescription = loginDescription;
          formInfo.fieldInfos.push(new FieldInfo(passwordDescription, passwordField));
          const parent = passwordField.getParent();
          if (parent)
          {
            const sameParentInputFields = inputFields.filter((inputField) => inputField.getParent() === parent);
            if (sameParentInputFields.length > 1)
            {
              const selectedInputField = sameParentInputFields.reduce((previous, current) =>
              {
                const value = this.scraper.getAttributeValuesForMatcher(Matcher.propertiesAll, current.getHtmlElement());
                const featuresValue = this.getFeaturesForValue(value, Matcher.manualWordsLogin);
                current.manualLoginScore = featuresValue.filter((_) => _ === 1).length;
                if (!previous)
                {
                  return current;
                }
                if (previous.manualLoginScore >= current.manualLoginScore)
                {
                  return previous;
                }

                return current;
              }, undefined);
              if (selectedInputField && selectedInputField.manualLoginScore > 0)
              {
                formInfo.fieldInfos.push(new FieldInfo(usernameDescription, selectedInputField));
              }
            }
            else if (sameParentInputFields.length === 1)
            {
              formInfo.fieldInfos.push(new FieldInfo(usernameDescription, sameParentInputFields[0]));
            }
          }
          tempFormInfos.push(formInfo);
        });
        result = tempFormInfos.reduce((previous, current/** , index, array*/) =>
        {
          if (!previous)
          {
            return current;
          }
          if (previous.fieldInfos.length >= current.fieldInfos.length)
          {
            return previous;
          }

          return current;
        }, undefined);
      }
    }
    return result;
  }

  argmax(arr)
  {
    if (arr.length === 0)
    {
      return -1;
    }
    let max = arr[0];
    let maxIndex = 0;
    for (let i = 1; i < arr.length; i++)
    {
      if (arr[i] > max)
      {
        maxIndex = i;
        max = arr[i];
      }
    }
    return maxIndex;
  }

  /**
   * Viterbi function done with arrays instead of math.js library and matrixes.
   * @param {[[number]]} transitionProbs
   * @param {[[number]]} observations
   * @param {[[number]]} weights
   * @param {[string]} classes
   * @returns [string] possible field type guesses
   */
  viterbi(transitionProbs, observations, weights, classes)
  {
    if (observations.length === 0)
    {
      return [];
    }
    const states = this.matrixMultiply(observations, weights);
    let cur = states[0];
    const stack = [];
    for (let state = 1; state < observations.length; state++)
    {
      let curCon = [];
      for (let j = 0; j < transitionProbs.length; j++)
      {
        curCon = curCon.concat([cur]);
      }
      const transProbs = this.matrixTranspose(this.matrixAdd(this.matrixTranspose(transitionProbs), curCon));
      const nextState = states[state];
      let statesCon = [];
      for (let j = 0; j < transitionProbs.length; j++)
      {
        statesCon = statesCon.concat([nextState]);
      }
      cur = this.matrixAdd(transProbs, statesCon);
      const maxColIxs = [];
      for (const p of this.matrixTranspose(cur))
      {
        const max = this.argmax(p);
        maxColIxs.push(max);
      }
      stack.push(maxColIxs);
      cur = this.matrixMax(cur, 0);
    }
    const stateSeq = [this.argmax(cur)];
    while (stack.length > 0)
    {
      const maxColIxs = stack.pop();
      const nextIndex = stateSeq.slice(-1)[0];
      const nextMaxCol = maxColIxs[nextIndex];
      stateSeq.push(nextMaxCol);
    }
    const result = stateSeq.reverse();
    const output = [];
    for (const index of result)
    {
      output.push(classes[index]);
    }
    return output;
  }

  getExceptions(formName)
  {
    const url = window.location.href;
    const { hostname } = new URL(url);
    if (this.exceptions)
    {
      return this.exceptions.filter((e) =>
      {
        try
        {
          const exceptionHostname = new URL(e.url).hostname;
          return (e.type === formName && exceptionHostname === hostname);
        }
        catch (error)
        {
          if (e.url.indexOf('http') !== 0)
          {
            try
            {
              const exceptionHostname = new URL(`https://${e.url}`).hostname;
              return (e.type === formName && exceptionHostname === hostname);
            }
            catch (error)
            {
              Globals.Logger.log(`invalid url ${e.url}`);
              return false;
            }
          }
          return false;
        }
      });
    }

    return [];
  }

  getFormInfoFromException(exception, formDescription)
  {
    Globals.Logger.log('getFormInfoFromException: start');
    const formInfo = new FormInfo();
    formInfo.formDescription = formDescription;
    formInfo.exception = true;
    formDescription.fields.forEach((fieldDescription) =>
    {
      const { name } = fieldDescription;
      const xpath = exception[name];

      if (xpath)
      {
        const elements = Xpath.getElementsByXPath(document, xpath);
        // elements = elements.filter(e => self.scraper.getFieldType(e) !== FieldType.Anything);
        if (elements && elements.length > 0)
        {
          const element = elements[0];
          if (FDHtmlElement.isHiddenFromScreen(element) === false || exception.is_hidden === true)
          {
            const currentFields = this.inputFields.concat(this.passwordFields).concat(this.clickableFields);
            let field = currentFields.find((_) => _.getHtmlElement() === element);
            if (!field)
            {
              if (fieldDescription.type === FieldType.Button)
              {
                field = new ClickableField(undefined, element);
              }
              else
              {
                field = new Field(undefined, element, fieldDescription.type);
              }
            }
            const fieldInfo = new FieldInfo(fieldDescription, field);
            formInfo.fieldInfos.push(fieldInfo);
          }
        }
      }
    });
    return formInfo.fieldInfos.length > 0 ? formInfo : undefined;
  }

  getFeaturesForField(field)
  {
    const features = this.getAllFeatures(field);
    return features;
  }

  getFeaturesForValue(value, matcher, negative = false)
  {
    const yes = negative === false ? 1 : 0;
    const no = negative === false ? 0 : 1;
    const features = [];
    matcher.forEach((featureWords) =>
    {
      if (featureWords)
      {
        let result = no;
        for (let i = 0; i < featureWords.length; i++)
        {
          const featureRegex = new RegExp(featureWords[i], 'i');
          if (featureRegex.test(value))
          {
            result = yes;
            break;
          }
        }
        features.push(result);
      }
    });
    return features;
  }

  getCRFParams()
  {
    const { weights } = this.crfParams;

    if (weights.length === this.getNumFeatures())
    {
      return this.crfParams;
    }

    return Globals.CrfParams;
  }

  setCrfParams(crf)
  {
    this.crfParams = crf;
  }

  getAllFeatures(field)
  {
    const words = Matcher.wordsAll;
    const value = this.scraper.getAttributeValuesForMatcher(Matcher.propertiesAll, field.getHtmlElement());
    const featuresValue = this.getFeaturesForValue(value, words);
    const features = featuresValue
      .concat(this.getFeaturesFieldType(field))
      .concat(this.getFeaturesParentComposition(field))
      .concat(this.getFeaturesParent(field))
      .concat(this.getFeaturesSurrounding(field))
      .concat(this.getFeaturesUrl());
    return features;
  }

  getAllFeaturesDescription()
  {
    const words = Matcher.wordsAll;
    const features = words.map((_) => _.join(','))
      .concat(this.getFeaturesFieldTypeDesc())
      .concat(this.getFeaturesParentCompositionDesc())
      .concat(this.getFeaturesParentDesc())
      .concat(this.getFeaturesSurroundingDesc())
      .concat(this.getFeaturesUrlDesc());
    return features;
  }

  getFeaturesFieldType(field)
  {
    return [
      FDBoolean.toBit((field.getType() === FieldType.Text)),
      FDBoolean.toBit((field.getType() === FieldType.Password)),
      FDBoolean.toBit((field.getType() === FieldType.Button)),
      FDBoolean.toBit((field.getType() === FieldType.Select))
    ];
  }

  getFeaturesFieldTypeDesc()
  {
    return [
      '(field.getType() === FieldType.Text)',
      '(field.getType() === FieldType.Password)',
      '(field.getType() === FieldType.Button)',
      '(field.getType() === FieldType.Select)'
    ];
  }

  getFeaturesParent(field)
  {
    let formValue = '';
    if (field.getParent() !== undefined)
    {
      formValue = this.scraper.getAttributeValuesForMatcher(Matcher.propertiesForm, field.getParent());
    }
    return this.getFeaturesForValue(formValue, Matcher.formWords);
  }

  getFeaturesParentDesc()
  {
    return Matcher.formWords.map((_) => `Parent Form: ${_.join(',')}`);
  }

  getFeaturesParentComposition(field)
  {
    const parentPasswordFieldsCount = field.getParent() ? FDHtmlElement.passwordFieldsCount(field.getParent()) : 0;
    const parentInputfieldsCount = field.getParent() ? FDHtmlElement.inputFieldsCount(field.getParent()) : 0;
    return [
      FDBoolean.toBit((field.getParent() !== undefined)),
      FDBoolean.toBit((field.getParent() instanceof HTMLFormElement === true)),
      FDBoolean.toBit((parentPasswordFieldsCount > 0)),
      FDBoolean.toBit((parentPasswordFieldsCount > 1)),
      FDBoolean.toBit((parentInputfieldsCount > 0)),
      FDBoolean.toBit((parentInputfieldsCount === 2)),
      FDBoolean.toBit((parentInputfieldsCount === 3)),
      FDBoolean.toBit((parentInputfieldsCount > 3)),
    ];
  }

  getFeaturesParentCompositionDesc()
  {
    return [
      'FDBoolean.toBit((field.getParent() !== undefined))',
      'FDBoolean.toBit((field.getParent() instanceof HTMLFormElement === true))',
      'FDBoolean.toBit((parentPasswordFieldsCount > 0))',
      'FDBoolean.toBit((parentPasswordFieldsCount > 1))',
      'FDBoolean.toBit((parentInputfieldsCount > 0))',
      'FDBoolean.toBit((parentInputfieldsCount === 2))',
      'FDBoolean.toBit((parentInputfieldsCount === 3))',
      'FDBoolean.toBit((parentInputfieldsCount > 3))',
    ];
  }

  getFeaturesSurrounding(field)
  {
    const surroundingStrings = this.scraper.getFieldSurrounding(field);
    const surroundingValue = surroundingStrings.join(';');
    return this.getFeaturesForValue(surroundingValue, Matcher.surroundingWords);
  }

  getFeaturesSurroundingDesc()
  {
    return Matcher.surroundingWords.map((_) => `Surrounding: ${_.join(',')}`);
  }

  getFeaturesUrl()
  {
    return this.getFeaturesForValue(`${FDWindow.getOriginUrlForTraining()};${window.location.href}`, Matcher.urlWords);
  }

  getFeaturesUrlDesc()
  {
    return Matcher.urlWords.map((_) => `Url: ${_.join(',')}`);
  }

  setTraining(fields)
  {
    this.fieldsTraining.removeAll();
    fields.forEach((f) =>
    {
      this.fieldsTraining.push(f);
    });
  }

  cancelTraining()
  {
    this.fieldsTraining.removeAll();
    this.bearifier.cancelTraining();
  }

  getTrainerValues()
  {
    const exceptions = [];
    const ignores = [];
    this.fieldsTraining.forEach((f) =>
    {
      if (f.trainingHtmlElement)
      {
        const select = f.trainingHtmlElement;
        const xpath = Xpath.getElementXPath(f.getHtmlElement());
        if (select.selectedIndex > 0)
        {
          if (select.selectedIndex === 1)
          {
            ignores.push(xpath);
          }
          else
          {
            const { value } = select.options[select.selectedIndex];
            const formType = value.split(':')[0];
            const fieldType = value.split(':')[1];
            const defaultForm = {
              key: new URL(window.location.href).hostname,
              type: formType,
              html: '',
              raw_html: '',
              is_hidden: false,
              url: window.location.href,
              ignore: [],
              ignore_trainer: false
            };
            const exception = exceptions.find((_) => _.type === formType) || defaultForm;
            exception[fieldType] = xpath;
            if (exception === defaultForm)
            {
              exceptions.push(exception);
            }
          }
        }
      }
    });
    exceptions.forEach((ex) =>
    {
      ex.ignore = ignores;
    });
    if (exceptions.length === 0)
    {
      const defaultForm = {
        key: window.location.hostname,
        type: 'login',
        html: '',
        raw_html: '',
        is_hidden: false,
        url: window.location.href,
        ignore: ignores,
        ignore_trainer: false
      };
      exceptions.push(defaultForm);
    }
    return exceptions;
  }

  /**
   * !!NOTE ON FUNCTIONS BELOW!!
   * The following functions are helper functions needed to complete the matrix calculations in this class. These are implemented
   * here so we can remove the imported mathjs library and reduce the form detection package size. These functions are not
   * designed to be complete 1 to 1 replacements for the corresponding mathjs functions that they are replacing, but are
   * instead designed to fit the specific types of calculations needed by each, for our purposes in this class.
   * 1-D list == Array [1,2,3,4] (MxN = 1x4)
   * 2-D Matrix == Multidimensional Array [[1,2], [3,4]] (MxN = 2x2) | [[1,2,3,4]] (MxN = 1x4)
   */

  /**
   * Add together matrix + matrix, matrix + number, list + list, list + number. (Matrices must be of same size & only works on 2-D matrices.)
   * @param {[[number]] | [number] | number} a M*N matrix (2-D), list(1-D), or a single number
   * @param {[[number]] | [number] | number} b M*N matrix (2-D), list(1-D), or a single number
   * @returns {[[number]] | [number]} result of the matrix addition.
   */
  matrixAdd(a, b)
  {
    let ans;

    // Find out what A is
    const aType = typeof (a) === 'number' ? 'number'
      : Array.isArray(a) && typeof (a[0]) === 'number' ? '1-D'
        : Array.isArray(a) && Array.isArray(a[0]) && typeof (a[0][0]) === 'number' ? '2-D'
          : undefined;

    // Find out what B is
    const bType = typeof (b) === 'number' ? 'number'
      : Array.isArray(b) && typeof (b[0]) === 'number' ? '1-D'
        : Array.isArray(b) && Array.isArray(b[0]) && typeof (b[0][0]) === 'number' ? '2-D'
          : undefined;

    if (aType === '2-D' && bType === '2-D') // Both 2-D matrixes
    {
      ans = Array.from(a);
      ans = ans.map((arr, i) => arr.map((num, j) => num + b[i][j]));
    }
    else if (aType === '2-D' && bType === 'number') // 1st arg 2-D matrix, 2nd arg number
    {
      ans = Array.from(a);
      ans = ans.map((arr) => arr.map((num) => num + b));
    }
    else if (aType === 'number' && bType === '2-D') // 1st arg number, 2nd 2-D matrix
    {
      ans = Array.from(b);
      ans = ans.map((arr) => arr.map((num) => num + a));
    }
    else if (aType === '1-D' && bType === '1-D' && a.length === b.length) // Both 1-D matrixes
    {
      ans = Array.from(a);
      ans = ans.map((val, i) => val + b[i]);
    }
    else if (aType === '1-D' && bType === 'number') // 1st arg 1-D matrix, 2nd arg number
    {
      ans = Array.from(a);
      ans = ans.map((val) => val + b);
    }
    else if (aType === 'number' && bType === '1-D') // 1st arg number, 2nd 1-D matrix
    {
      ans = Array.from(b);
      ans = ans.map((val) => val + a);
    }
    else if (aType === bType) // different sized matrices are not valid
    {
      Globals.Logger.error('Different sized matrices are not supported');
    }
    else // any other combination is not valid
    {
      Globals.Logger.error(`unsopported types used: a: ${aType}, b: ${bType}`);
    }

    return ans;
  }

  /**
   * Takes either a list, or a 2-D matrix and provides the max, row max, or column max
   * @param {[number] | [[number]]} A 1-D list or 2-D Matrix
   * @param {number} dim undefined 0, or 1. With dim = 0 it takes the max of each column and with dim = 1 it takes the max of each row.
   * @returns the max, wor max or column max
   */
  matrixMax(A, dim)
  {
    const aType = Array.isArray(A) && typeof (A[0]) === 'number' ? '1-D'
      : Array.isArray(A) && Array.isArray(A[0]) && typeof (A[0][0]) === 'number' ? '2-D'
        : undefined;

    if (!aType)
    {
      Globals.Logger.error('A param is of incorrect type. Must be 1-D or 2-D array');
      return undefined;
    }

    if (dim === undefined)
    {
      if (aType === '1-D')
      {
        return A.reduce((a, b) => Math.max(a, b), -Infinity);
      }

      return A.flat().reduce((a, b) => Math.max(a, b), -Infinity);
    }
    if (dim === 0)
    {
      if (aType === '1-D')
      {
        return A.reduce((a, b) => Math.max(a, b), -Infinity);
      }

      const maxArr = Array.from(A[0]);
      for (let row = 1; row < A.length; row++)
      {
        for (let col = 0; col < A[row].length; col++)
        {
          maxArr[col] = Math.max(maxArr[col], A[row][col]);
        }
      }
      return maxArr;
    }
    if (dim === 1 && aType === '2-D')
    {
      const maxArr = new Array(A.length);
      maxArr.fill(-Infinity);
      for (let row = 0; row < A.length; row++)
      {
        for (let col = 0; col < A[row].length; col++)
        {
          maxArr[row] = Math.max(maxArr[row], A[row][col]);
        }
      }
      return maxArr;
    }
    Globals.Logger.error('problem with either incorrect dim value (undefined, 0, or 1) or matrix type (1-D or 2-D)');
    return undefined;
  }

  /**
   * Multiply two matrices together MxN * NxO or two lists.
   * @param {[[number]] | [number]} A - MxN Matrix (2-D) or list(1-D)
   * @param {[[number]] | [number]} B - NxO Matrix (2-D) or list(1-D)
   * @returns MxO Matrix(2-D) or list(1-D)
   */
  matrixMultiply(A, B)
  {
    // Find out what A is
    const aType = Array.isArray(A) && typeof (A[0]) === 'number' ? '1-D'
      : Array.isArray(A) && Array.isArray(A[0]) && typeof (A[0][0]) === 'number' ? '2-D'
        : undefined;

    // Find out what B is
    const bType = Array.isArray(B) && typeof (B[0]) === 'number' ? '1-D'
      : Array.isArray(B) && Array.isArray(B[0]) && typeof (B[0][0]) === 'number' ? '2-D'
        : undefined;

    // if both are 1-D
    if (aType === '1-D' && bType === '1-D' && A.length === B.length)
    {
      let result = 0;
      for (let i = 0; i < A.length; i++)
      {
        result += A[i] * B[i];
      }
      return result;
    }
    // if both are 2-D
    if (aType === '2-D' && bType === '2-D' && A[0].length === B.length)
    {
      const resRows = A.length;
      const aCols = A[0].length;
      const resCols = B[0].length;
      const result = new Array(resRows);
      for (let i = 0; i < result.length; i++)
      {
        result[i] = new Array(resCols);
        result[i].fill(0);
      }

      for (let row = 0; row < resRows; row++)
      {
        for (let col = 0; col < resCols; col++)
        {
          for (let acc = 0; acc < aCols; acc++)
          {
            result[row][col] += A[row][acc] * B[acc][col];
          }
        }
      }
      return result;
    }
    Globals.Logger.error('Unable to multiply the two matricies. Most likely incorrect dimensions or mismath in types (1-D) and (2-D)');
    return undefined;
  }

  /**
   * Transpose a matrix
   * @param {[[number]]} A - MxN Matrix (2-D)
   * @returns {[[number]]} Transpose of A (NxM Matrix)
   */
  matrixTranspose(A)
  {
    // Find out what A is
    const aType = Array.isArray(A) && Array.isArray(A[0]) && typeof (A[0][0]) === 'number' ? '2-D'
      : undefined;

    if (!aType)
    {
      Globals.Logger.error('incorrect type of A param');
      return undefined;
    }

    const result = new Array(A[0].length);
    for (let i = 0; i < result.length; i++)
    {
      result[i] = new Array(A.length);
      result[i].fill(0);
    }

    for (let row = 0; row < result.length; row++)
    {
      for (let col = 0; col < result[row].length; col++)
      {
        result[row][col] = A[col][row];
      }
    }

    return result;
  }
}
