/* eslint-disable no-console */
/**
 * Fallback for when no logger is passed into the library from the caller.
 */
export class Logger
{
  log(message)
  {
    console.log(message);
  }

  error(message)
  {
    console.error(message);
  }

  warn(message)
  {
    console.warn(message);
  }

  debug(message)
  {
    console.debug(message);
  }
}
