import { ContentCommands } from '@extension/common/commands';
import BackgroundIPC from '@extension/common/background_ipc';
// import { Logger } from '@extension/common/logger';
import FDContentListener from '@mcafee/form-detection/index';
import { Globals as FDGlobals } from '@mcafee/form-detection/globals';
import { FieldType, VaultItemType } from '@mcafee/form-detection/trainerModels';
import ScriptBase from '@extension/scripts/content_base';
import Utils from '@extension/utilities/utils';
import { CommonUtils } from '@mcafee/form-detection/utils';

export default class IframeFormDetection extends ScriptBase
{
  constructor()
  {
    super(ContentCommands.PING_IFRAME_FORM_DETECTION);
    this.debouncedOnSubmitFormInfo = Utils.debounce(this.onSubmitFormInfo.bind(this), 500, true);
    this.fdContentListener = new FDContentListener(this.debouncedOnSubmitFormInfo);
    this.apsEnabled = false; // Flag for whether aps is enabled or not
  }

  async main()
  {
    // BkGlobals will determine if the IDPS feature should be shown
    const promises = [BackgroundIPC.getBkGlobals(), BackgroundIPC.getFDWeights(), BackgroundIPC.getFDExceptions(), BackgroundIPC.getFDCrfParams()];
    const [bkGlobals, fdWeights, fdExceptions, fdCrfParams] = await Promise.all(promises);

    if (!bkGlobals)
    {
      // Logger.warn('startFormDetection: Failed to retrieved bkGlobals');
      return;
    }

    const { IDPS_FEATURE_ENABLED, APS_COHORT } = bkGlobals.nativeSettings;
    const idpsFeatureEnabled = bkGlobals.IDPS_IS_ENTITLED && IDPS_FEATURE_ENABLED.value > 0;
    const config = { allowLocalhost: bkGlobals.ALLOW_LOCALHOST, weights: fdWeights, /* logger: Logger*/ exceptions: fdExceptions, crfParams: fdCrfParams };
    this.apsEnabled = APS_COHORT.value > 0;

    this.fdContentListener.init(config);
    this.startFdContentListener(idpsFeatureEnabled);
  }

  /**
   * This method will invoke a document level observer that watches for DOM changes on the current page,
   * and will trigger the corresponding events if either any login, signup, or changepassword forms are detected.
   */
  startFdContentListener(submitListener = false)
  {
    // Initial call to get original page type.
    this.getPageInfo(submitListener);

    // Defining Observer to listen for page changes.
    const observer = new MutationObserver(() =>
    {
      // disconnect observer if more than 80 (CPU usage spikes in getPageInfo while guessing fields)
      if (this.fdContentListener.fields.length > 80)
      {
        // Logger.log('stopping observer, (too many fields)');
        observer.disconnect();
      }

      // Only need to proceed when this method returns true and there are new or modified form fields.
      if (this.fdContentListener.shouldGetPageInfo())
      {
        this.getPageInfo(submitListener);
      }
    });

    const observerConfig = { childList: true, subtree: true };
    observer.observe(document, observerConfig);
  }

  /**
   * Gets all pageinfo for the current page, and also adds submission listeners if specified.
   * @param {Boolean} onSubmit - If this param is set to true, then it will add submission listeners for when the forms on the page are submitted.
   */
  getPageInfo(onSubmit = true)
  {
    // Get login/signup fields and forms for current page.
    this.fdContentListener.autoFill.clearFormInfos();
    let pageInfo = this.fdContentListener.autoFill.getPageInfo();

    // Whenever no forms are found, but there is a text field, password field, and button field found,
    // we re-get the pageinfo with the forced login flag true. This should help us to detect more login pages.
    const hasTextField = pageInfo.fields.some((field) => field.type === FieldType.Text);
    const hasPasswordField = pageInfo.fields.some((field) => field.type === FieldType.Password);
    const hasClickableField = pageInfo.fields.some((field) => field.type === FieldType.Button);
    if (pageInfo.forms.length === 0 && ((hasTextField && hasClickableField) || (hasPasswordField && hasClickableField)))
    {
      pageInfo = this.fdContentListener.autoFill.getPageInfo(true);
    }

    if (!pageInfo || !pageInfo.forms) return;
    this.fdContentListener.pageInfoForms = pageInfo.forms;

    this.fdContentListener.pageInfoForms && this.fdContentListener.pageInfoForms.forEach((formInfo) =>
    {
      // Get all fieldinfo.field.FDProcessedIDs, sort them, and concatenate them into a string, to be used for comparisons.
      // This is how we identify if formInfos are the same, instead of trying to do some type of object compare.
      const newFormInfoCompareStr = formInfo.fieldInfos.map((fieldInfo) => fieldInfo.field.FDProcessedID).sort().join('');

      if (this.fdContentListener.doesFormHaveSubmissionListeners(newFormInfoCompareStr)) return;

      if (formInfo.formDescription.vaultItemType !== VaultItemType.LoginItem) return;

      this.fdContentListener.formSubmissionListeners.push(newFormInfoCompareStr);

      if (formInfo.formDescription.name === 'signup')
      {
        this.signupFormFound(formInfo);
      }

      if (onSubmit)
      {
        this.fdContentListener.addSubmissionListeners(formInfo);
      }
    });
  }

  /**
   * Handles desired functionality for when a signup form is detected on the page.
   */
  signupFormFound()
  {
    if (this.apsEnabled)
    {
      BackgroundIPC.signUpFormDetected();
    }
  }

  /**
   * Handles desired behaviour for when a login, signup, or changepassword is submitted.
   * @param {*} _ - js event that triggered the listener
   * @param {*} formInfo - formInfo object corresponding to the submit button or key press event that triggered this function
   * @returns boolean
   */
  onSubmitFormInfo(_, formInfo)
  {
    const isHostNameInvalid = window.location.hostname === '' || window.location.hostname === 'localhost';
    if (isHostNameInvalid && !FDGlobals.AllowLocalHost)
    {
      // Logger.log('Ignoring localhost');
      return;
    }

    if (formInfo.formDescription.vaultItemType !== VaultItemType.LoginItem)
    {
      // Logger.log('Ignoring form submit');
      return;
    }

    const vaultItem = this.fdContentListener.autoFill.getVaultItemFromFormInfo(formInfo);

    if (formInfo.formDescription.name === 'login' || formInfo.formDescription.name === 'signup' || formInfo.formDescription.name === 'changepassword')
    {
      const loginItem = vaultItem;
      const usernameField = formInfo.fieldInfos.find((_) => _.fieldDescription.name === 'username');
      const passwordField = formInfo.fieldInfos.find((_) => ['password', 'newpassword', 'confirmpassword'].includes(_.fieldDescription.name));

      if (usernameField && passwordField)
      {
        // Logger.debug('[Form Detection - Index] Both username and password field are present.');
      }

      // GMAIL HACK: When trying to create an account on account.google.com and a gmail email option is selected, the email value will not contain the email domain (@gmail.com)
      if (window.location.hostname === 'accounts.google.com' && loginItem && loginItem.metadata && !CommonUtils.validateEmailFormat(loginItem.metadata.username))
      {
        loginItem.metadata.username = `${loginItem.metadata.username}@gmail.com`;
      }

      // Both username/email and password field are available. login
      if (usernameField && passwordField && loginItem.content && loginItem.content.password && loginItem.metadata && loginItem.metadata.username)
      {
        if (!CommonUtils.validateEmailFormat(loginItem.metadata.username) && formInfo.formDescription.name === 'login')
        {
          // Logger.debug('[Form Detection - Index] The username field does not contain an email address, it may be a phone number or an username');
          return;
        }
        BackgroundIPC.saveFormInfo(loginItem.metadata.username.toLowerCase(), this.fdContentListener.scraper.hostname);
        return;
      }
      // Only username/email is available.
      if (usernameField && loginItem.metadata && loginItem.metadata.username && !passwordField)
      {
        // Logger.debug('[Form Detection - Index] Only the username field was detected');
        const data = {
          username: loginItem.metadata.username.toLowerCase(),
          hostname: this.fdContentListener.scraper.hostname
        };
        BackgroundIPC.saveMultiStepLogin(data, false);
        return;
      }
      // Only password available, send to background
      if (!usernameField && passwordField && loginItem.content)
      {
        // Logger.debug('[Form Detection - Index] Only the password field was detected.');
        const data = {
          hostname: this.fdContentListener.scraper.hostname
        };
        BackgroundIPC.saveMultiStepLogin(data, true);
      }

      // Logger.log("[Form Detection - Index] Didn't do anything with vaultitem, is it a mistake? Unable to find username or password input values.");
    }
  }
}

const iframeFormDetection = new IframeFormDetection();
iframeFormDetection.main();
