import ScrapingServiceImpl from './scraper';
import ScoringServiceImpl from './scoring';
import AutoFillImpl from './autofill';
import { Globals } from './globals';
import { Logger } from './logger';
import { FDHtmlElement } from './utils';
import { FieldType } from './trainerModels';

export default class FDContentListener
{
  constructor(onSubmitFormInfo = undefined)
  {
    this.scraper = null;
    this.scoreService = null;
    this.autoFill = null;
    this.fields = [];
    this.pageInfoForms = null;
    this.submissionListeners = []; // Array contains the id's of elements with form detection submission listeners attached.
    this.formSubmissionListeners = []; // Array containing the compare strings of formInfo objects that already have submission event listeners attached to them.
    this.onSubmitFormInfo = onSubmitFormInfo; // Callback function passed in by caller on object creation to handle functionality for when a form is submitted.
  }

  init(config)
  {
    // Globals need to be initialized before these other clases as they may require them.
    Globals.AllowLocalHost = config.allowLocalhost || false;
    Globals.Logger = config.logger || new Logger();
    Globals.Weights = config.weights || null;
    Globals.Exceptions = config.exceptions || null;
    Globals.CrfParams = config.crfParams || null;

    this.scraper = new ScrapingServiceImpl();
    this.scoreService = new ScoringServiceImpl();
    this.autoFill = new AutoFillImpl();

    if (!this.onSubmitFormInfo)
    {
      this.onSubmitFormInfo = () =>
      {
        Globals.Logger.log('No form submission callback function was provided on object creation.');
      };
    }
  }

  /**
   * Get the formtypes found on the current page.
   * @returns Promise that resolve to a string array containing the different formtypes found on the page.
   */
  getPageType()
  {
    const promise = new Promise((accept) =>
    {
      const retrievePageTypes = (tries) =>
      {
        if (tries < 6 && this.pageInfoForms === null)
        {
          setTimeout(() =>
          {
            retrievePageTypes(tries + 1);
          }, 500);
        }
        else if (this.pageInfoForms === null || this.pageInfoForms.length === 0)
        {
          accept([]);
        }
        else
        {
          const formTypes = this.pageInfoForms.map((formInfo) => formInfo.formDescription.name);
          accept(formTypes);
        }
      };

      retrievePageTypes(0);
    });

    return promise;
  }

  /**
   * Scrape the webpage and retrieve information about form fields.
   */
  shouldGetPageInfo()
  {
    this.scraper.clearFields();
    let hasChanged = false;
    const newFields = this.scraper.getAllFields();

    // Determine change based on mismatch of # form fields
    if (this.fields.length !== newFields.length)
    {
      hasChanged = true;
    }

    // Check if any of the items from the local list isn't in new list
    if (!hasChanged)
    {
      // seems like we can guarantee the same order when scraping
      // the longer the list the more we'll skip
      let itemsToSkip = Math.floor(Math.log2(this.fields.length));
      if (itemsToSkip < 1) // Skipping can cause us to miss out on form field changes sometimes, but does bring performance benefits.
      {
        itemsToSkip = 1;
      }

      for (let i = 0; i < this.fields.length; i += itemsToSkip)
      {
        if (this.fields[i].getHtmlElement() !== newFields[i].getHtmlElement())
        {
          hasChanged = true;
          break;
        }
      }
    }

    if (hasChanged)
    {
      this.fields = newFields;
    }

    return hasChanged;
  }

  /**
   * Add submission listeners to a form.
   * @param {*} formInfo - formInfo object for the form we want to add listeners to.
   * @returns
   */
  addSubmissionListeners(formInfo)
  {
    // Assign click events to the submit buttons
    let formHasSubmit = false;
    for (const { field, fieldDescription } of formInfo.fieldInfos)
    {
      if (fieldDescription.type === FieldType.Button && this.submissionListeners.indexOf(field.FDProcessedID) === -1)
      {
        // Add element to submission listeners list, then add listener.
        this.submissionListeners.push(field.FDProcessedID);
        field.getHtmlElement().addEventListener('click', (e) =>
        {
          this.onSubmitFormInfo(e, formInfo);
        });
        formHasSubmit = true;
      }

      // Put a listener on the parent of the field if it is a form element.
      const parent = field.getParent();
      if (parent && parent.tagName === 'FORM')
      {
        const parentFDProcessedID = FDHtmlElement.addOrGetFDProcessedID(parent);
        if (this.submissionListeners.indexOf(parentFDProcessedID) === -1)
        {
          this.submissionListeners.push(parentFDProcessedID);
          parent.addEventListener('submit', (e) =>
          {
            this.onSubmitFormInfo(e, formInfo);
          });

          // Since the parent is a form element then we will also check if there are any
          // uncaught submission elements or possible submit buttons within it.
          if (parent.elements && parent.elements.length > 0)
          {
            for (let i = 0; i < parent.elements.length; i++)
            {
              if (parent.elements[i].type === 'submit')
              {
                // check if this element has been added already, if not add a listener to it.
                const curElement = parent.elements[i];
                const curElementFDProcessedID = FDHtmlElement.addOrGetFDProcessedID(curElement);
                if (this.submissionListeners.indexOf(curElementFDProcessedID) === -1)
                {
                  this.submissionListeners.push(curElementFDProcessedID);
                  // Add a click listener to that button
                  curElement.addEventListener('click', (e) =>
                  {
                    this.onSubmitFormInfo(e, formInfo);
                  });
                  formHasSubmit = true;
                }
              }
            }
          }
        }
      }
    }

    // If no submit buttons are found for the form, then we use this as a last ditch effort to catch submission.
    // Once a keypress occurs on an input field, we add a listener to call submit the form info before the page unloads.
    if (!formHasSubmit)
    {
      for (const { field } of formInfo.fieldInfos)
      {
        if (this.submissionListeners.indexOf(field.FDProcessedID) === -1)
        {
          // Add element to submission listeners list, then add listener.
          this.submissionListeners.push(field.FDProcessedID);
          field.getHtmlElement().addEventListener('keypress', (e) =>
          {
            // When page unloads
            window.onbeforeunload = () =>
            {
              this.onSubmitFormInfo(e, formInfo);
            };
          }, { once: true });
        }
      }
    }

    // We want the same behaviour for when submitting a form using the enter key, as clicking the submit button,
    // so in order to have that, we are adding the keypress listeners here too.
    document.addEventListener('keypress', (e) =>
    {
      this.onTriggerKeyPressFormInfo(e, formInfo);
    });
  }

  /**
   * Will check if window keypress listener has already been added for the corresponding formInfo object, using the forminfoCompareStr of that object.
   * @param {String} formInfoCompareStr - Identifier created for each formInfo object by getting the FDProcessedIDs, sorting them, and concatenate them into a string.
   * @returns Boolean indicating whether or not the listener has already been added. True if already added, false otherwise.
   */
  doesFormHaveSubmissionListeners(formInfoCompareStr)
  {
    for (let i = 0; i < this.formSubmissionListeners.length; i++)
    {
      if (formInfoCompareStr === this.formSubmissionListeners[i] || this.formSubmissionListeners[i].includes(formInfoCompareStr))
      {
        return true;
      }
    }
    return false;
  }

  /**
   * Process the form only if the enter key was pressed
   * @param {*} event - javascript event parameter passed back from keypress listener
   * @param {*} formInfo - formInfo object
   */
  onTriggerKeyPressFormInfo(event, formInfo)
  {
    if (event.code === 'Enter')
    {
      this.onSubmitFormInfo(event, formInfo);
    }
  }
}
