import { FieldType, VaultItemType, FieldDescription } from './trainerModels';
import { LoginItem, LoginItemMetadata, FieldInfo } from './models';
import ScoringServiceImpl from './scoring';
import ScrapingServiceImpl from './scraper';
import { FDHtmlElement, FDWindow } from './utils';
import { Globals } from './globals';

export default class AutoFillImpl
{
  constructor()
  {
    this.currentPageInfo = undefined;
    this.currentLoginItem = undefined;
    this.pendingAutofillPassword = false;
  }

  clearFormInfos()
  {
    this.currentPageInfo = undefined;
  }

  getPageInfo(forcedLogin = false)
  {
    if (!this.currentPageInfo || forcedLogin === true)
    {
      this.currentPageInfo = new ScoringServiceImpl().getPageInfo(forcedLogin);
    }

    return this.currentPageInfo;
  }

  getFormInfos(forcedLogin = false)
  {
    return this.getPageInfo(forcedLogin).forms;
  }

  fillContentVaultItem(vaultItem)
  {
    if (vaultItem.metadata.type === VaultItemType.LoginItem)
    {
      if (vaultItem.queryMatch)
      {
        if (new URL(vaultItem.queryMatch).hostname !== window.location.hostname)
        {
          if (vaultItem.whitelistMatch)
          {
            const hasHostNameInWhitelist = vaultItem.whitelistMatch.some((hostname) => hostname === window.location.hostname);
            if (!hasHostNameInWhitelist)
            {
              Globals.Logger.log(`Not autofilling - no whitelist match${window.location.href}`);
              return;
            }
          }
          else
          {
            Globals.Logger.log(`Not autofilling - no queryMatch${window.location.href}`);
            return;
          }
        }
      }
      const loginItem = vaultItem;
      this.fillLoginItem(loginItem);
    }
    else if (vaultItem.metadata.type === VaultItemType.CreditCardItem)
    {
      const cc = vaultItem;
      this.fillCreditCardItem(cc);
    }
  }

  fillLoginItemIfPending()
  {
    if (this.pendingAutofillPassword === true && this.currentLoginItem)
    {
      this.pendingAutofillPassword = false;
      this.fillLoginItem(this.currentLoginItem, true, true);
      return true;
    }
    return false;
  }

  fillLoginItem(loginItem, autoSubmit = false, loginInfo = undefined, fieldInfo = undefined)
  {
    // This function has been updated to use the current formInfo object associated to the login item being passed
    // as a first choice, then will fall back to the logic below if one is not provided.
    if (loginInfo === undefined)
    {
      let formInfos = this.getFormInfos();
      if (formInfos.length === 0)
      {
        this.clearFormInfos();
        formInfos = this.getFormInfos(true);
      }
      loginInfo = formInfos
        .sort((a, b) => b.formDescription.priority - a.formDescription.priority)
        .find((_) => _.formDescription.vaultItemType === VaultItemType.LoginItem);
    }

    if (loginInfo)
    {
      loginInfo.vaultItem = loginItem;
      this.currentLoginItem = loginItem;
      const loginFieldMap = this.getAllLoginAndSignupFields(loginInfo, loginItem, fieldInfo, true);
      loginFieldMap.forEach((el) =>
      {
        const { field } = el;
        const { value } = el;
        this.pendingAutofillPassword = false;
        if (field && value && field.field.getEditableHtmlElement().value !== value)
        {
          this.setValueForFieldInfo(field, value);
          if (el.type !== 'password' && el.type !== 'newpassword')
          {
            this.pendingAutofillPassword = true;
          }
        }
      });
      const usernameField = loginInfo.fieldInfos.find((_) => _.fieldDescription.name === 'username');
      if (!loginItem.metadata.username && usernameField && usernameField.field.getHtmlElement().innerText === '')
      {
        return;
      }

      const submitFields = loginInfo.fieldInfos.filter((_) => _.fieldDescription.name === 'submit');
      autoSubmit = autoSubmit && !loginInfo.grabParentForms().some((_) => FDHtmlElement.inputFieldsCount(_) >= 2 || FDHtmlElement.passwordFieldsCount(_) > 1 || FDHtmlElement.hasCaptcha(_) === true);
      if (autoSubmit === true && submitFields.length > 0)
      {
        let foundButton = false;
        if (fieldInfo)
        {
          const parent = fieldInfo.field.getParent();
          if (parent)
          {
            const submitField = loginInfo.fieldInfos.find((f) => f.field.getParent() === parent && f.fieldDescription.name === 'submit');
            if (submitField)
            {
              foundButton = true;
              // This has to appear as a real mouse click and not a simmulated one to trigger the onSubmitForm() in TrueKeyMV3.
              FDHtmlElement.performRealMouseClick(submitField.field.getHtmlElement());
            }
          }
        }
        if (!foundButton)
        {
          // This has to appear as a real mouse click and not a simmulated one to trigger the onSubmitForm() in TrueKeyMV3.
          FDHtmlElement.performRealMouseClick(submitFields[0].field.getHtmlElement());
        }
      }
      // If auto submit is enabled and no submit fields were found, we will try and find a submit field via the parent form,
      // or press the enter key on the password input box, or the username input box to do the autosubmit instead.
      // This will make the autologon more robust.
      else if (autoSubmit)
      {
        const passwordField = loginInfo.fieldInfos.find((_) => _.fieldDescription.name === 'password');

        const parent = passwordField && passwordField.field.getParent() ? passwordField.field.getParent()
          : usernameField && usernameField.field.getParent() ? usernameField.field.getParent()
            : null;

        if (parent && parent.tagName === 'FORM' && parent.elements && parent.elements.length > 0)
        {
          for (let i = 0; i < parent.elements.length; i++)
          {
            if (parent.elements[i].type === 'submit')
            {
              // click this element.
              FDHtmlElement.performRealMouseClick(parent.elements[i]);
              break;
            }
          }
        }

        else if (passwordField)
        {
          FDHtmlElement.performEnterKeyPress(passwordField.field.getHtmlElement());
        }
        else if (usernameField)
        {
          FDHtmlElement.performEnterKeyPress(usernameField.field.getHtmlElement());
        }
      }
    }
  }

  getFieldForDescription(formInfo, fieldDescription)
  {
    return formInfo.fieldInfos.find((_) => _.fieldDescription.name === fieldDescription);
  }

  getAllLoginAndSignupFields(formInfo, loginItem, clickedField = undefined, autofilling = false)
  {
    const allLoginAndSignupFields = [];
    let currentType = '';
    let currentField;
    let currentValue = '';

    (formInfo.fieldInfos.filter((_) => _.fieldDescription.name.includes('username'))).forEach((field) =>
    {
      currentType = 'username';
      currentField = field;
      currentValue = loginItem && loginItem.metadata.username;
      allLoginAndSignupFields.push({ type: currentType, field: currentField, value: currentValue });
    });

    (formInfo.fieldInfos.filter((_) => _.fieldDescription.name.includes('password') && _.fieldDescription.name !== 'newpassword' && _.fieldDescription.name !== 'confirmpassword')).forEach((field) =>
    {
      currentType = 'password';
      currentField = field;
      if (autofilling === true || (clickedField && clickedField.fieldDescription.name !== 'newpassword' && clickedField.fieldDescription.name !== 'confirmpassword'))
      {
        currentValue = loginItem.content.password;
      }
      else
      {
        currentValue = field.field.getEditableHtmlElement().value;
      }
      allLoginAndSignupFields.push({ type: currentType, field: currentField, value: currentValue });
    });

    if (formInfo.formDescription.name === 'changepassword')
    {
      (formInfo.fieldInfos.filter((_) => _.fieldDescription.name.includes('password') && _.fieldDescription.name !== 'oldpassword')).forEach((field) =>
      {
        currentType = 'newpassword';
        currentField = field;
        if (clickedField && (clickedField.fieldDescription.name === 'newpassword' || clickedField.fieldDescription.name === 'confirmpassword'))
        {
          currentValue = loginItem.content.password;
        }
        else
        {
          currentValue = field.field.getEditableHtmlElement().value;
        }
        allLoginAndSignupFields.push({ type: currentType, field: currentField, value: currentValue });
      });
    }
    return allLoginAndSignupFields;
  }

  fillCreditCardItem(ccItem)
  {
    let formInfos = this.getFormInfos();
    if (formInfos.length === 0)
    {
      this.clearFormInfos();
      formInfos = this.getFormInfos(false);
    }

    const ccInfo = formInfos
      .sort((a, b) => b.formDescription.priority - a.formDescription.priority)
      .find((_) => _.formDescription.vaultItemType === VaultItemType.CreditCardItem);

    if (ccInfo)
    {
      ccInfo.vaultItem = ccItem;
      const fieldCCMap = [
        [this.getFieldForDescription(ccInfo, 'cc_number'), ccItem.content.number],
        [this.getFieldForDescription(ccInfo, 'cc_name'), ccItem.content.cardholder],
        [this.getFieldForDescription(ccInfo, 'cc_mm'), ccItem.content.expiryMonth],
        [this.getFieldForDescription(ccInfo, 'cc_yy'), ccItem.content.expiryYear],
        [this.getFieldForDescription(ccInfo, 'cc_cvc'), ccItem.content.verification],
        [this.getFieldForDescription(ccInfo, 'cc_date'), `${ccItem.content.expiryMonth}-${ccItem.content.expiryYear}`]
      ];
      fieldCCMap.forEach((el) =>
      {
        const field = el[0];
        const value = el[1];
        if (field && value)
        {
          this.setValueForFieldInfo(field, value);
        }
      });
    }
  }

  setValueForField(field, value)
  {
    const htmlElement = field.getEditableHtmlElement();
    try
    {
      htmlElement.click(); // Click may fail and we don't want whole script to crash becasue of this.
      htmlElement.value = value.toString();
      FDHtmlElement.triggerValueChange(htmlElement);
    }
    catch (e)
    {
      Globals.Logger.log(e.message);
    }
  }

  setValueForFieldInfo(fieldInfo, value)
  {
    // This will return an empty object if field element is not editable. In those cases the script would crash as click unable to be processed.
    const htmlElement = fieldInfo.field.getEditableHtmlElement();
    try
    {
      htmlElement.click(); // Click may fail and we don't want whole script to crash becasue of this.

      let filled = false;
      if (fieldInfo.fieldDescription.name === 'cc_yy')
      {
        filled = this.setSpecialValueForCCYear(htmlElement, value.toString());
      }
      else if (fieldInfo.fieldDescription.name === 'cc_mm')
      {
        const valueAsNumber = value;
        filled = this.setSpecialValueForCCMonth(htmlElement, valueAsNumber);
      }
      else if (fieldInfo.fieldDescription.name === 'cc_date')
      {
        filled = this.setSpecialValueForExpiryDate(htmlElement, value.toString());
      }
      if (filled === false)
      {
        htmlElement.value = value.toString();
      }
      FDHtmlElement.triggerValueChange(htmlElement);
    }
    catch (e)
    {
      Globals.Logger.log(e.message);
    }
  }

  setSpecialValueForCCYear(htmlElement, value)
  {
    if (htmlElement instanceof HTMLSelectElement)
    {
      if (htmlElement.options.length > 1)
      {
        const firstOrSecondValue = htmlElement.options.item(1).value;
        if (firstOrSecondValue)
        {
          if (firstOrSecondValue.length === 2 && value.length === 4)
          {
            htmlElement.value = value.substring(2);
            return true;
          }
        }
      }
    }
    return false;
  }

  setSpecialValueForCCMonth(htmlElement, value)
  {
    if (htmlElement instanceof HTMLSelectElement)
    {
      const lastValue = htmlElement.options.item(htmlElement.options.length - 1).value;
      if (lastValue)
      {
        if (lastValue.length > 2)
        {
          for (let i = 0; i < htmlElement.options.length; i++)
          {
            const option = htmlElement.options[i];
            if (this.isMMMfromMM(option.value, value) === true)
            {
              option.selected = true;
              return true;
            }
          }
        }
      }
    }
    return false;
  }

  setSpecialValueForExpiryDate(htmlElement, value)
  {
    if (htmlElement instanceof HTMLInputElement)
    {
      const splitValue = value.split('-');
      if (htmlElement.size === 4 || htmlElement.maxLength === 4)
      {
        htmlElement.value = splitValue[0] + splitValue[1].substring(2);
        return true;
      }
      if (htmlElement.size === 6 || htmlElement.maxLength === 6)
      {
        htmlElement.value = splitValue[0] + splitValue[1];
        return true;
      }
    }
    return false;
  }

  isMMMfromMM(mmm, mm)
  {
    const allMmmTomm = [
      ['jan', 1],
      ['feb', 2],
      ['mar', 3],
      ['apr', 4],
      ['may', 5],
      ['jun', 6],
      ['jul', 7],
      ['aug', 8],
      ['sep', 9],
      ['oct', 10],
      ['nov', 11],
      ['dec', 12]
    ];
    const mmmTomm = allMmmTomm.firstOrDefault((_) => mmm.toLowerCase().indexOf(_[0]) !== -1, undefined);
    return mmmTomm && mmmTomm[1] === mm;
  }

  grabUnAssignedPasswordFieldAfter(currentPasswordField)
  {
    const pageInfo = this.getPageInfo();
    const allPasswordFields = pageInfo.fields.filter((f) => f.getType() === FieldType.Password);
    const alreadyAssignedPasswordFields = pageInfo.forms.map((formInfo) => formInfo.fieldInfos.filter((f) => f.field.getType() === FieldType.Password)).flatMap((array) => array).map((fieldInfo) => fieldInfo.field);
    const unAssignedPasswordFields = [];
    allPasswordFields.forEach((pw) =>
    {
      if (alreadyAssignedPasswordFields.some((f) => f.getHtmlElement() === pw.getHtmlElement()) === false)
      {
        if (pw.getHtmlElement().compareDocumentPosition(currentPasswordField.getHtmlElement()) === Node.DOCUMENT_POSITION_PRECEDING)
        {
          unAssignedPasswordFields.push(pw);
        }
      }
    });

    if (unAssignedPasswordFields.length > 0)
    {
      return unAssignedPasswordFields[0];
    }

    return undefined;
  }

  fillPassword(password, fieldInfo = undefined)
  {
    const formInfos = this.getFormInfos();
    const signupForms = formInfos.filter((f) => f.formDescription.name === 'signup');
    const self = this;
    let passwordFields = [];
    if (fieldInfo)
    {
      const formInfo = formInfos.find((f) => f.fieldInfos.some((fi) => fi.field.getHtmlElement() === fieldInfo.field.getHtmlElement()));
      if (formInfo && formInfo.formDescription.name === 'signup' && fieldInfo.fieldDescription.name === 'password')
      {
        // Have to convert fieldInfo's to field types so they can be consumed by the setValue function to set the passwords.
        passwordFields.push(fieldInfo.field);
        fieldInfo.field.sibling && passwordFields.push(fieldInfo.field.sibling.field);
      }
      else if (formInfo && formInfo.formDescription.name === 'changepassword' && fieldInfo.fieldDescription.name === 'oldpassword')
      {
        passwordFields.push(fieldInfo.field);
      }
      else if (formInfo && formInfo.formDescription.name === 'changepassword')
      {
        passwordFields = formInfo.fieldInfos.filter((field) => field.fieldDescription.name === 'newpassword' || field.fieldDescription.name === 'confirmpassword').map((_) => _.field);
      }
      else if (fieldInfo.field.getParent())
      {
        passwordFields = new ScrapingServiceImpl().getPasswordFields(fieldInfo.field.getParent());
      }
      else
      {
        passwordFields = formInfo.fieldInfos.filter((f) => f.field.getType() === FieldType.Password).map((_) => _.field);
        if (passwordFields.length < 2)
        {
          const potentialSecondPasswordField = this.grabUnAssignedPasswordFieldAfter(passwordFields[0]);
          if (potentialSecondPasswordField)
          {
            passwordFields.push(potentialSecondPasswordField);
          }
        }
      }
    }
    else if (signupForms.length > 0 && signupForms[0].fieldInfos.length > 0)
    {
      const parent = signupForms[0].fieldInfos[0].field.getParent();
      if (parent)
      {
        passwordFields = new ScrapingServiceImpl().getPasswordFields(fieldInfo.field.getParent());
      }
      else
      {
        passwordFields = signupForms[0].fieldInfos.filter((f) => f.field.getType() === FieldType.Password).map((_) => _.field);
        if (passwordFields.length < 2)
        {
          const potentialSecondPasswordField = this.grabUnAssignedPasswordFieldAfter(passwordFields[0]);
          if (potentialSecondPasswordField)
          {
            passwordFields.push(potentialSecondPasswordField);
          }
        }
      }
    }
    else
    {
      passwordFields = formInfos.flatMap((f) => f.fieldInfos).filter((f) => f.field.getType() === FieldType.Password).map((_) => _.field);
    }
    passwordFields.slice(0, 2).forEach((p) => self.setValueForField(p, password));
  }

  getSignupSecondPasswordField(formInfo, fieldInfo)
  {
    let passwordFields = [];
    if (fieldInfo.field.getParent())
    {
      passwordFields = new ScrapingServiceImpl().getPasswordFields(fieldInfo.field.getParent());
    }
    else
    {
      passwordFields = formInfo.fieldInfos.filter((f) => f.field.getType() === FieldType.Password).map((_) => _.field);
      if (passwordFields.length < 2)
      {
        const potentialSecondPasswordField = this.grabUnAssignedPasswordFieldAfter(passwordFields[0]);
        if (potentialSecondPasswordField)
        {
          passwordFields.push(potentialSecondPasswordField);
        }
      }
    }
    passwordFields = passwordFields.filter((passwordField) => passwordField.FDProcessedID !== fieldInfo.field.FDProcessedID);

    if (passwordFields.length > 0)
    {
      // convert password field here to FieldInfo object
      const passwordFieldDescription = new FieldDescription('confirmpassword', FieldType.Password);
      const passwordFieldInfo = new FieldInfo(passwordFieldDescription, passwordFields[0]);

      return passwordFieldInfo;
    }
    return null;
  }

  getLoginItem(pageInfo, formInfo)
  {
    const loginItem = new LoginItem();
    loginItem.changed = true;
    const originalItem = !formInfo ? undefined : formInfo.vaultItem !== undefined ? formInfo.vaultItem : undefined;

    const favicon = FDWindow.getFavicon();
    if (pageInfo.url)
    {
      const host = new URL(pageInfo.url).hostname;
      const name = originalItem ? originalItem.metadata.name : host;
      const frameHost = new URL(window.location.href).hostname;
      if (host !== frameHost)
      {
        loginItem.metadata = new LoginItemMetadata(pageInfo.url, name, pageInfo.url, favicon, frameHost);
      }
      else
      {
        loginItem.metadata = new LoginItemMetadata(pageInfo.url, name, pageInfo.url, favicon);
      }
    }
    else
    {
      const name = originalItem ? originalItem.metadata.name : window.location.host;
      loginItem.metadata = new LoginItemMetadata(window.location.href, name, window.location.href, favicon);
    }

    const loginFieldMap = this.getAllLoginAndSignupFields(formInfo, loginItem);
    const usernameField = loginFieldMap.find((_) => _.type === 'username');
    const passwordField = loginFieldMap.find((_) => _.type === 'password');
    const newPasswordField = loginFieldMap.find((_) => _.type === 'newpassword');

    if (usernameField)
    {
      loginItem.metadata.username = usernameField.field.field.getEditableHtmlElement().value;
    }

    if (newPasswordField)
    {
      loginItem.content.password = newPasswordField.field.field.getEditableHtmlElement().value;
    }

    else if (passwordField)
    {
      loginItem.content.password = passwordField.field.field.getEditableHtmlElement().value;
    }

    if (formInfo.formDescription.name === 'signup')
    {
      if (!this.currentPageInfo.forms.some((_) => _.formDescription.name === 'login'))
      {
        loginItem.metadata.loginUrl = '';
      }
    }

    loginItem.metadata.type = 'LoginItem';
    return [loginItem, originalItem];
  }

  getVaultItemFromFormInfo(formInfo)
  {
    // Returns a tuple
    const [loginItem, originalItem] = this.getLoginItem(this.currentPageInfo, formInfo);

    if (originalItem !== undefined)
    {
      loginItem.changed = !LoginItem.isSame(loginItem, originalItem);
    }

    return loginItem;
  }
}
