import { FieldType } from './trainerModels';
import { ClickableField, Field } from './models';
import * as Matcher from './matcher';
import { FDHtmlElement } from './utils';

export default class ScrapingServiceImpl
{
  constructor()
  {
    this.textFields = undefined;
    this.passwordFields = undefined;
    this.selectFields = undefined;
    this.clickeableFields = undefined;
    this.hostname = new URL(window.location.href).hostname;
  }

  _storeFieldInfo(parent, element, result, type)
  {
    if (parent && parent instanceof HTMLFormElement)
    {
      result.push(new Field(parent, element, type));
    }
    else
    {
      const offsetParentDiv = FDHtmlElement.getOffsetDiv(element);
      result.push(new Field(offsetParentDiv, element, type));
    }
  }

  _storeClickableFieldInfo(parent, element, result)
  {
    if (parent && parent instanceof HTMLFormElement)
    {
      result.push(new ClickableField(parent, element));
    }
    else
    {
      if (element.offsetParent && element.offsetParent instanceof HTMLDivElement)
      {
        const parentDiv = element.offsetParent;
        result.push(new ClickableField(parentDiv, element));
      }
      else
      {
        result.push(new ClickableField(undefined, element));
      }
    }
  }

  clearFields()
  {
    this.textFields = undefined;
    this.passwordFields = undefined;
    this.clickeableFields = undefined;
    this.selectFields = undefined;
  }

  getAllFields()
  {
    const inputFields = this.getAllTextFields();
    const selectFields = this.getAllSelectFields();
    const passwordFields = this.getAllPasswordFields();
    const clickableFields = this.getAllClickeableFields();
    const fields = [...inputFields, ...selectFields, ...passwordFields, ...clickableFields].sort((a, b) =>
    {
      if (a.getHtmlElement().compareDocumentPosition(b.getHtmlElement()) === Node.DOCUMENT_POSITION_PRECEDING)
      {
        return 1;
      }
      return -1;
    });

    return fields;
  }

  getAllTextFields()
  {
    // Early return cached results
    if (this.textFields && this.textFields.length > 0)
    {
      return this.textFields;
    }

    let result = [];

    // Parse individual form tags for input tag children
    const forms = document.getElementsByTagName('form');
    for (let i = 0; i < forms.length; i++)
    {
      result = result.concat(this.getTextFields(forms[i]));
    }

    // Parse entire document for input tags
    const inputs = this.getTextFields();
    for (let i = 0; i < inputs.length; i++)
    {
      // Avoid storing duplicates
      if (!result.some((f) => f.getHtmlElement() === inputs[i].getHtmlElement()))
      {
        result.push(inputs[i]);
      }
    }

    this.textFields = result; // Store results in-memory
    return result;
  }

  getTextFields(htmlElement)
  {
    const result = [];
    const inputTypes = ['text', 'email', 'tel', 'number'];
    const elements = htmlElement
      ? Array.from(htmlElement.getElementsByTagName('input'))
      : Array.from(document.getElementsByTagName('input'));

    for (const element of elements)
    {
      const isInputTypeValid = inputTypes.includes(element.type.toLowerCase());
      if (isInputTypeValid && !FDHtmlElement.isHiddenFromScreen(element))
      {
        this._storeFieldInfo(htmlElement, element, result, FieldType.Text);
      }
    }

    return result;
  }

  getAllSelectFields()
  {
    if (this.selectFields && this.selectFields.length > 0)
    {
      return this.selectFields;
    }

    let result = [];
    const forms = Array.from(document.getElementsByTagName('form'));
    for (const form of forms)
    {
      result = result.concat(this.getSelectFields(form));
    }

    const inputs = this.getSelectFields();
    for (let i = 0; i < inputs.length; i++)
    {
      if (!result.some((f) => f.getHtmlElement() === inputs[i].getHtmlElement()))
      {
        result.push(inputs[i]);
      }
    }

    this.selectFields = result;
    return result;
  }

  getSelectFields(htmlElement)
  {
    const result = [];
    const elements = htmlElement
      ? Array.from(htmlElement.getElementsByTagName('select'))
      : Array.from(document.getElementsByTagName('select'));

    for (const element of elements)
    {
      if (!FDHtmlElement.isHiddenFromScreen(element) && element.name !== 'remembearTrainerSelect')
      {
        this._storeFieldInfo(htmlElement, element, result, FieldType.Select);
      }
    }

    return result;
  }

  getAllPasswordFields()
  {
    if (this.passwordFields && this.passwordFields.length > 0)
    {
      return this.passwordFields;
    }

    let result = [];
    const forms = Array.from(document.getElementsByTagName('form'));
    for (const form of forms)
    {
      result = result.concat(this.getPasswordFields(form));
    }

    const inputs = this.getPasswordFields();
    for (let i = 0; i < inputs.length; i++)
    {
      if (!result.some((f) => f.getHtmlElement() === inputs[i].getHtmlElement()))
      {
        result.push(inputs[i]);
      }
    }

    this.passwordFields = result;
    return result;
  }

  getPasswordFields(htmlElement)
  {
    const result = [];
    const elements = htmlElement
      ? Array.from(htmlElement.getElementsByTagName('input'))
      : Array.from(document.getElementsByTagName('input'));

    for (const element of elements)
    {
      if (!FDHtmlElement.isHiddenFromScreen(element) && element.type.toLowerCase() === 'password')
      {
        this._storeFieldInfo(htmlElement, element, result, FieldType.Password);
      }
    }

    return result;
  }

  getAllClickeableFields()
  {
    if (this.clickeableFields && this.clickeableFields.length > 0)
    {
      return this.clickeableFields;
    }

    let result = [];
    const forms = Array.from(document.getElementsByTagName('form'));
    for (const form of forms)
    {
      result = result.concat(this.getClickeableInputFields(form));
      result = result.concat(this.getDivButtons(form));
    }

    const inputs = this.getClickeableInputFields();
    for (let i = 0; i < inputs.length; i++)
    {
      if (!result.some((f) => f.getHtmlElement() === inputs[i].getHtmlElement()))
      {
        result.push(inputs[i]);
      }
    }

    const divs = this.getDivButtons();
    for (let j = 0; j < divs.length; j++)
    {
      if (!result.some((f) => f.getHtmlElement() === divs[j].getHtmlElement()))
      {
        result.push(divs[j]);
      }
    }

    this.clickeableFields = result;
    return result;
  }

  getDivButtons(htmlElement)
  {
    const result = [];
    const node = htmlElement || document;
    const array = Array.from(node.querySelectorAll('div[role=\'button\']'));
    for (const element of array)
    {
      if (
        !this.shouldIgnoreButton(element)
        && !FDHtmlElement.isHiddenFromScreen(element)
        && this.getFieldType(element) === FieldType.Button
      )
      {
        this._storeClickableFieldInfo(htmlElement, element, result);
      }
    }

    return result;
  }

  getClickeableInputFields(htmlElement)
  {
    const result = [];
    const node = htmlElement || document;
    const inputs = Array.from(node.getElementsByTagName('input'));
    const buttons = Array.from(node.getElementsByTagName('button'));
    const clickableElements = [...inputs, ...buttons];

    for (const element of clickableElements)
    {
      if (
        !this.shouldIgnoreButton(element)
        && !FDHtmlElement.isHiddenFromScreen(element)
        && this.getFieldType(element) === FieldType.Button
      )
      {
        this._storeClickableFieldInfo(htmlElement, element, result);
      }
    }

    return result;
  }

  shouldIgnoreButton(element)
  {
    if (element.className === 'Cy' && this.hostname === 'hangouts.google.com' && element instanceof HTMLButtonElement)
    {
      return true;
    }

    if (element.name && element.name.includes('remembearTrainer'))
    {
      return true;
    }

    if (element.disabled)
    {
      return true;
    }

    return false;
  }

  getAttributeValues(matcher, element)
  {
    const result = [];

    for (let i = 0; i < matcher.length; i++)
    {
      const attributeName = matcher[i];
      let value = element.getAttribute(attributeName);

      if (!value)
      {
        value = element[attributeName];
      }

      if (value !== undefined)
      {
        result.push(value);
      }
    }

    return result;
  }

  getAttributeValuesForMatcher(matcher, element)
  {
    const properties = this.getAttributeValues(matcher, element);
    let value = '';

    for (let propIndex = 0; propIndex < properties.length; propIndex++)
    {
      value += `${properties[propIndex]} `;
    }

    if (value !== undefined)
    {
      return value.toLowerCase();
    }

    return '';
  }

  getFieldFormSurrounding(field)
  {
    // const countPrevious = 0;
    // const countNext = 0;
    // const maxTraversal = 10;
    const result = [];
    if (!field.getParent())
    {
      return result;
    }

    const list = field.getParent().children;
    for (let i = 0; i < list.length; i++)
    {
      const field = list.item(i);
      if (field && !(field instanceof HTMLScriptElement || field instanceof HTMLFontElement))
      {
        result.push(this.getAttributeValuesForMatcher(Matcher.propertiesOther, field));
      }
    }

    const parentOfParent = field.getParent().parentElement;
    if (parentOfParent && parentOfParent.children.length > 1 && parentOfParent.children.length < 10)
    {
      const { children } = parentOfParent;
      for (let i = 0; i < children.length; i++)
      {
        const field = children.item(i);
        if (field)
        {
          result.push(this.getAttributeValuesForMatcher(Matcher.propertiesOther, field));
        }
      }
    }

    return result;
  }

  getFieldSurrounding(field)
  {
    return this.getFieldSurroundingDOM(field).concat(this.getFieldSurroundingVisual(field));
  }

  getFieldSurroundingDOM(field)
  {
    const countPrevious = 0;
    const countNext = 0;
    const maxTraversal = 8;
    const result = [];
    let nextField = field.getHtmlElement().nextElementSibling;
    let previousField = field.getHtmlElement().previousElementSibling;

    while (nextField && countNext <= maxTraversal)
    {
      result.push(this.getAttributeValuesForMatcher(Matcher.propertiesOther, nextField));
      nextField = nextField.nextElementSibling;
    }

    while (previousField && countPrevious <= maxTraversal)
    {
      result.push(this.getAttributeValuesForMatcher(Matcher.propertiesOther, previousField));
      previousField = previousField.previousElementSibling;
    }

    return result;
  }

  getFieldSurroundingVisual(field)
  {
    const maxHeightOnTopAndBottomOfField = 75;
    const maxWidthOnLeftAndRightOfField = 75;
    const maxUpwardTraversal = 3;
    let countNext = 0;
    const result = [];
    const fieldRect = field.getHtmlElement().getBoundingClientRect();
    const zone = {
      bottom: fieldRect.bottom + maxHeightOnTopAndBottomOfField,
      height: fieldRect.height + maxHeightOnTopAndBottomOfField * 2,
      left: fieldRect.left - maxWidthOnLeftAndRightOfField,
      right: fieldRect.right + maxWidthOnLeftAndRightOfField,
      top: fieldRect.top - maxHeightOnTopAndBottomOfField,
      width: fieldRect.width + maxWidthOnLeftAndRightOfField * 2,
    };
    let nextParent = field.getHtmlElement().parentElement;

    while (nextParent.parentElement && countNext <= maxUpwardTraversal)
    {
      nextParent = nextParent.parentElement;
      if (FDHtmlElement.intersectWithClientRect(nextParent, zone)) countNext++;
    }

    const htmlElementsInZone = this.getHtmlElementsThatIntersect(zone, field.getHtmlElement(), nextParent);
    htmlElementsInZone.forEach((element) =>
    {
      result.push(this.getAttributeValuesForMatcher(Matcher.propertiesOther, element));
    });

    return result;
  }

  getHtmlElementsThatIntersect(zone, notHtmlElement, parentToLookIn)
  {
    let result = [];
    const { children } = parentToLookIn;
    for (let i = 0; i < children.length; i++)
    {
      const htmlElement = children.item(i);
      if (htmlElement instanceof HTMLDivElement && htmlElement !== notHtmlElement && FDHtmlElement.intersectWithClientRect(htmlElement, zone))
      {
        result = result.concat(this.getHtmlElementsThatIntersect(zone, notHtmlElement, htmlElement));
      }
      else if (htmlElement instanceof HTMLElement && FDHtmlElement.intersectWithClientRect(htmlElement, zone))
      {
        result.push(htmlElement);
      }
    }
    return result;
  }

  getFieldType(htmlElement)
  {
    if (htmlElement instanceof HTMLInputElement)
    {
      if (htmlElement.type.toLowerCase() === 'text'
                || htmlElement.type.toLowerCase() === 'email'
                || htmlElement.type.toLowerCase() === 'tel'
                || htmlElement.type.toLowerCase() === 'number')
      {
        return FieldType.Text;
      }
      if (htmlElement.type.toLowerCase() === 'password')
      {
        return FieldType.Password;
      }
      if (htmlElement.type.toLowerCase() === 'submit'
                || htmlElement.type.toLowerCase() === 'image'
                || htmlElement.type.toLowerCase() === 'button')
      {
        return FieldType.Button;
      }
    }
    else if (htmlElement instanceof HTMLSelectElement)
    {
      return FieldType.Select;
    }
    else if (htmlElement instanceof HTMLButtonElement
            || (htmlElement instanceof HTMLDivElement && htmlElement.getAttribute('role') === 'button'))
    {
      return FieldType.Button;
    }
    return FieldType.Anything;
  }
}
